import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import FileUploader from "Component/FileUploader";
import ReactExport from "react-export-excel";
import { getWarehouses } from "apiServices";
import "./index.scss";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;
const Upload = (props) => {
  const dispatch = useDispatch();
  const warehousesData = useSelector((state) => state.warehouseData);
  const Field = [
    {
      dataField: "id", //field ini wajib ada untuk editable column
      text: "ID",
      editable: false,
      sort: true,
      hidden: true,
      type: "number",
    },
    {
      dataField: "warehouse",
      text: "Warehouse",
      type: "string",
      sort: true,
    },
    {
      dataField: "deliveryDate",
      text: "Delivery Date",
      type: "date_indo",
      sort: true,
    },
    {
      dataField: "slot",
      text: "Slot",
      type: "number",
      sort: true,
    },
    {
      dataField: "skuNumber",
      text: "SKU Number",
      type: "number",
      sort: true,
    },
    {
      dataField: "skuDescription",
      text: "SKU Description",
      type: "string",
      sort: true,
    },
    { dataField: "uos", text: "Target UOS", type: "number", sort: true },
    { dataField: "uom", text: "Target UOM", type: "number", sort: true },
  ];

  const dataSet1 = [
    {
      data: "",
    },
  ];

  useEffect(() => {
    if (!warehousesData) {
      getWarehouses({ dispatch });
    }
  }, []);

  return (
    <div className="mb-5">
      {/* export this table to excel */}
      <ExcelFile
        element={
          //dont change id and style please
          <button id="download-template-xls" style={{ display: "none" }}>
            Download Data
          </button>
        }
        //file name without types
        filename="TargetRepackUpload_Template"
      >
        <ExcelSheet data={dataSet1} name="template">
          <ExcelColumn label="Warehouse" value="data" />
          <ExcelColumn label="Delivery Date" value="data" />
          <ExcelColumn label="Slot" value="data" />
          <ExcelColumn label="SKU Number" value="data" />
          <ExcelColumn label="SKU Description" value="data" />
          <ExcelColumn label="Target UOS" value="data" />
          <ExcelColumn label="Target UOM" value="data" />
        </ExcelSheet>
      </ExcelFile>
      {/* end export this table to excel */}

      <FileUploader
        nextPage={() =>
          props.history.push(
            "/inventory-entry/repacking/upload-target-repack/review"
          )
        }
        transaction={"repacking"}
        field={Field}
        type="repacking"
      />
    </div>
  );
};

export default withRouter(Upload);
